import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Link from './link';


const useStyles = makeStyles(theme => ({
    grow: {
      flexGrow: 1,
    },
    icon: {
      marginRight: theme.spacing(2),
    },
    link: {
      color: "#ffffff",
      margin: theme.spacing(1, 1.5), // https://material-ui.com/customization/spacing/
    },
    sectionDesktop: {
      display: 'none',
      [theme.breakpoints.up('md')]: {
        display: 'flex',
      },
    },
    sectionMobile: {
      display: 'flex',
      [theme.breakpoints.up('md')]: {
        display: 'none',
      },
    },
    padding: {
      padding: theme.spacing(3),
    },
}));

interface HeaderProps {
  tabIndex: number;
}

export default function Header(props: HeaderProps) {
  const classes = useStyles({});
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const [value, setValue] = React.useState(props.tabIndex < 0 ? false : props.tabIndex);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  // const handleProfileMenuOpen = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const handleChange = (_: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };


  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Profile</MenuItem>
      <MenuItem onClick={handleMenuClose}>My account</MenuItem>
    </Menu>
  );

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem component={Link} href='/tutroials'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>影片教學</p>
      </MenuItem>
      <MenuItem component={Link} href='/installers'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>推薦安裝包</p>
      </MenuItem>
      <MenuItem component={Link} href='/faq'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>常見問題</p>
      </MenuItem>
      <MenuItem component={Link} href='/changelogs'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>歷史版本</p>
      </MenuItem>
      <MenuItem component={Link} href='/about'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>關於</p>
      </MenuItem>
    </Menu>
  );

  interface StyledTabsProps {
    value: number;
    onChange: (event: React.ChangeEvent<{}>, newValue: number) => void;
  }

  const StyledTabs = withStyles({
    indicator: {
      display: 'flex',
      justifyContent: 'center',
      backgroundColor: 'transparent',
      '& > div': {
        maxWidth: 40,
        width: '100%',
        backgroundColor: '#635ee7',
      },
    },
  })((props: StyledTabsProps) => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);
  
  interface StyledTabProps {
    label: string;
    component: any;
    href: any;
  }

  const StyledTab = withStyles(theme => ({
    root: {
      minWidth: "auto",
      textTransform: 'none',
      color: '#fff',
      fontWeight: theme.typography.fontWeightRegular,
      fontSize: theme.typography.pxToRem(15),
      marginRight: theme.spacing(1),
      '&:focus': {
        opacity: 1,
      },
    },
  }))((props: StyledTabProps) => <Tab disableRipple {...props} />);

  return (
      <AppBar position="relative">
          <Toolbar>
              <img src="images/header/logo.png" style={{ marginRight: 16, width: 48, height: 48}}/>
              <Link 
                  aria-label="tutorial for minecraft scepter"
                  variant="h6"
                  href="/"
                  style={{color: "white"}}
                >
                  創世神權杖
              </Link>
              <div className={classes.grow} />
              <nav className={classes.sectionDesktop}>
                {
                // <Link 
                //   aria-label="tutorial for minecraft scepter"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   影片教學
                // </Link>
                // <Link 
                //   aria-label="recommend installer package"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   推薦安裝包
                // </Link>
                // <Link 
                //   aria-label="changelog of minecraft scepter"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   歷史版本
                // </Link>
                // <Link 
                //   aria-label="about the author"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   關於
                // </Link>
                }
                <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
                  <StyledTab label="影片教學" component={Link} href='/tutorials'/>
                  <StyledTab label="推薦安裝包" component={Link} href='/installers'/>
                  <StyledTab label="常見問題" component={Link} href='/faq'/>
                  <StyledTab label="歷史版本" component={Link} href='/changelogs'/>
                  <StyledTab label="關於" component={Link} href='/about'/>
                </StyledTabs>
                <Typography className={classes.padding} />
              </nav>
              <nav className={classes.sectionMobile}>
                <IconButton
                  aria-label="show more"
                  aria-controls={mobileMenuId}
                  aria-haspopup="true"
                  onClick={handleMobileMenuOpen}
                  color="inherit"
                >
                  <MoreIcon />
                </IconButton>
              </nav>
          </Toolbar>
          {renderMobileMenu}
          {renderMenu}
      </AppBar>
  );
}